.thanks{
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSU-ER4aXndxd6o545e6MvV4h3y7YJa7z93Gg&usqp=CAU");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;
    width: 500px;
    font-family: Berlin Sans FB;
    font-size: large;
    color: lightyellow;
}
.title-header-for-popup-thanks{
    font-size: 36px;
    color:fuchsia;
    background-color: unset !important;
}
.btnClose{
    position: absolute;
    top: 0px;
    right: 0px;
}
@media screen and (max-width: 500px) {
    .thanks{
        width: 350px;
    }
}
@media screen and (max-width: 350px) {
    .thanks{
        width: 250px;
    }
}
