.message{
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSU-ER4aXndxd6o545e6MvV4h3y7YJa7z93Gg&usqp=CAU");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;
    width: 500px;
    font-family: Berlin Sans FB;
    font-size: large;
    color: lightyellow;
}

textarea{
    width: 460px;
    max-height: 400px;
    min-height: 100px;
    border-color: cornflowerblue;
    font-size: 16px;
    font-family: Calibri;
    color: cornflowerblue;
}
textarea:focus-visible{
    outline-color: cornflowerblue;
}

.title-header-for-popup-message{
    font-size: 36px;
    color:fuchsia;
    background-color: unset !important;
}
.btnClose{
    position: absolute;
    top: 0px;
    right: 0px;
}
@media screen and (max-width: 500px) {
    .message{
        width: 350px;
    }
    textarea{
        width: 310px;
    }
}
@media screen and (max-width: 350px) {
    .message{
        width: 250px;
    }
    textarea{
        width: 210px;
    }
}
