.picture{
    object-fit: cover;
    display: block;
}
.picture-name{
    font-family: "Bodoni MT Black";
    font-size: 22px;
    color: darkblue;
}
.picture-info{
    font-family: "Bradley Hand ITC";
    font-size: 18px;
    font-weight: 600;
    color: darkslateblue;

}
.container1 {
    position: relative;
    width: fit-content;
}
.overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    /*background-color: red;*/
    /*background-image: url('/src/images/img1.jpg');*/
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    width: 300px;
    height: 0;
    transition: .5s ease;
}
.container1:hover .overlay {
    height: 425px;
}

/*responsive for mobile*/
@media screen and (max-width: 1000px) {
 .container1,.overlay,.picture{
  width: 220px;
 }
    .picture-name{
        font-size: 17px;
    }
}

@media screen and (max-width: 750px) {
    .container1,.overlay,.picture{
        width: 500px;
        height: 600px;
    }
    .picture-name{
        font-size: 36px;
    }
}
@media screen and (max-width: 550px) {
    .container1,.overlay,.picture{
        width: 350px;
        /*height: 600px;*/
    }
    .picture-name{
        font-size: 17px;
    }
}

@media screen and (max-width: 400px) {
    .container1,.overlay,.picture{
        width: 300px;
        height: 350px;
    }
}
@media screen and (max-width: 370px) {
    .container1,.overlay,.picture{
        width: 250px;
        height: 300px;
    }
}
