.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header-title{
  font-family: Vivaldi;
  font-size: 100px;
}

.font-style-for-content-header{

  font-size: 18px;
  color: lightyellow;
  font-weight: 600;
  font-style: italic;
}

.wuyi-art-max-width{
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;

}
.star:hover {
  transform: rotate(-15deg) scale(1.3)
}

.font-style-for-title-content{
  font-family: "Berlin Sans FB";
  font-size: 26px;
  color: lightyellow;
}
.style-for-totalStar{
  color: darkblue;
}
.btn-submit{
  color: lightyellow;
  border-color: lightyellow;
  background-color: darkblue;
}
.button-submit{
  background-color: #c2fbd7;
  border-radius: 50px;
  border-width: 0;
  box-shadow: rgba(25,25,25,.04) 0 0 1px 0,rgba(0,0,0,.1) 0 3px 4px 0;
  color: green;
  cursor: pointer;
  display: inline-block;
  font-family: Arial,sans-serif;
  font-size: 1em;
  height: 50px;
  padding: 0 25px;
  transition: all 200ms;
}
.button-submit:hover{
  background-color: #afe6c3;
  transform: scale(1.05);
}

.mymodal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  border: 1px solid #ccc;
  background: #fff;
  overflow: auto;
  border-radius: 4px;
  outline: none;
  /*padding: 20px;*/
}

.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: RGBA(204,170,139,0.43);
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

#your-comment{
  width: 500px;
  background-color: lightgoldenrodyellow;
  display: none;
  font-family: "Berlin Sans FB";
  font-size: 26px;
  color: darkblue;
  text-align: left;
}
.cmt{
  font-size: 16px;
}
.App-link {
  color: #61dafb;
}

.title-content{
  color:   #dc3545;
  font-size: 20px;
  height: fit-content;
  width: fit-content;
  font-family: "Berlin Sans FB";
}

.box .content {
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  /*background-image: url("https://wallpaperaccess.com/full/187170.jpg");*/
  background-size: cover;
}

.box {
  width: fit-content;
  height: fit-content;
  box-sizing: border-box;
  padding: 8px;
  position: relative;
  overflow: hidden;
}

.box::before {
  content: '';
  position: absolute;
  width: 160%;
  height: 800%;
  background: repeating-linear-gradient(
          white 0%,
          white 7.5px,
          #dc3545 7.5px,
          #dc3545 15px,
          white 15px,
          white 22.5px,
          #dc3545 22.5px,
          #dc3545 30px);
  transform: translateX(-20%) translateY(-20%) rotate(-45deg);
  animation: animate 20s linear infinite;
}

.box .content {
  position: relative;
  /*background-color: white;*/
  /*flex-direction: column;*/
  box-sizing: border-box;
  /*padding: 30px;*/
  text-align: center;
  /*font-family: sans-serif;*/
  z-index: 2;
}

.box,
.box .content {
  box-shadow: 0 0 2px #dc3545,
  0 0 5px rgba(0, 0, 0, 1),
  inset 0 0 5px rgba(0, 0, 0, 1);
  border-radius: 10px;
}

@keyframes animate {
  from {
    background-position: 0;
  }

  to {
    background-position: 0 450px;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 1000px) {
  .star{
    padding-right: 8px !important;
    padding-left: 8px !important;
  }
}
@media screen and (max-width: 750px) {
  .picture-list{
    display: grid !important;
  }
  .picture-detail{
    width: 100% !important;
  }
  .star{
    padding-right: 22px !important;
    padding-left: 22px !important;
  }

}

@media screen and (max-width: 550px) {
  #your-comment{
    width: 250px;
    font-size: 18px;
  }
  .header-title{
    font-size: 80px;
  }
  .font-style-for-content-header{
    font-size: 16px;
  }
  .box::before {
    height: 750%;
  }
}
@media screen and (max-width: 370px) {
  .star{
    padding-right: 10px !important;
    padding-left: 10px !important;
  }
  .header-title{
    font-size: 60px;
  }
  .font-style-for-content-header{
    font-size: 14px;
  }
  .box::before {
    height: 600%;
  }
}
